@import url('http://fonts.cdnfonts.com/css/garogier');

body{
  background: linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c);
}
.container{
  padding: 5%;
}
.parent {
  display: grid;
  max-width: 1000px;
  justify-content: center;
  margin: auto;
}

.section{
  text-align: center;
}

.logos {
  width: 60px;
  height: 60px;
  padding: 10px;
  object-fit: contain;
}
.boxContainer{
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
}
.linkText{
  text-decoration: 'none';
  color: black;
}
* {
  font-family: 'Garogier', sans-serif;
 }